import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import axios from "axios";
import { Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import Loading from "./Loading";
function Post() {
	const { id } = useParams();
	const apiUrl = "https://jsonplaceholder.typicode.com/posts/";
	const [post, setPost] = useState([]);
	const [showComments, setShowComments] = useState(false);
	const [comments, setComments] = useState({ loading: false, comments: [] });
	const history = useHistory();
	const [userId, setUserid] = useState("");
	function getData() {
		axios
			.get(apiUrl + id)
			.then((response) => {
				//console.log(response.data);
				setUserid(response.data.userId);
				setPost(response.data);
			})
			.catch((error) => {
				console.log(error);
			});
	}
	useEffect(() => {
		getData();
		return () => {
			setPost({}); // This worked for me
		};
	}, []);
	function getComments() {
		if (showComments === false) {
			if (comments.comments.length == 0) {
				setComments({ loading: true });
				axios
					.get("https://jsonplaceholder.typicode.com/comments?postid=" + id)
					.then((response) => {
						//console.log(response.data);
						setComments({ loading: false, comments: response.data });
					})
					.catch((error) => {
						console.log(error);
					});
			}
			setShowComments(true);
		} else {
			setShowComments(false);
		}
	}
	function deletePost() {
		axios
			.delete("https://jsonplaceholder.typicode.com/posts/" + id)
			.then((response) => {
				console.log(response);
				//window.location.reload();
				let path = "/posts/" + userId;
				history.push(path);
			})
			.catch((error) => {
				console.log(error);
			});
	}
	return (
		<div className="post">
			<h3>
				{post.title}{" "}
				<i
					className="delete icon"
					onClick={() => {
						deletePost();
					}}
				></i>
			</h3>
			<p>{post.body}</p>

			<Button
				variant="outlined"
				onClick={() => {
					getComments();
				}}
			>
				Show Comments
			</Button>
			{showComments === false ? (
				<div></div>
			) : (
				<div>
					{comments.loading === true ? (
						<Loading />
					) : (
						<div>
							{comments.comments.map((comment) => (
								<div className="ui comments" key={comment.id}>
									<div className="comment">
										<a class="avatar">
											<img src="data:image/png;base64," />
										</a>
										<div className="content">
											<a className="author">{comment.name}</a>
											<div className="metadata">
												<div className="date">{comment.email}</div>
											</div>
											<div className="text">{comment.body}</div>
										</div>
									</div>
								</div>
							))}
						</div>
					)}
				</div>
			)}
		</div>
	);
}

export default Post;
